:root {
  --link-color: #00acdc;
}

html {
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  margin: 0;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  margin: 10px 0;
  padding: 0 50px;
  display: flex;
}

.nav-logo a {
  align-items: center;
  display: flex;
}

img {
  width: 40px;
  height: 40px;
}

.nav {
  align-items: center;
  display: flex;
}

.nav-links {
  justify-content: flex-end;
  display: flex;
}

.nav-list {
  margin: 0;
  padding: 0;
  line-height: 60px;
  list-style: none;
}

li {
  text-align: center;
  height: 60px;
  margin-left: 17px;
  line-height: 60px;
  display: inline-block;
}

li:first-child {
  margin-left: 0;
}

.nav a {
  text-transform: uppercase;
  cursor: pointer;
  color: #000;
  text-decoration: none;
  display: flex;
}

.nav-item:first-of-type .nav__link {
  color: var(--link-color) !important;
}

a[data-qa="hover"]:hover {
  color: var(--link-color);
}

.is-active {
  color: var(--link-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--link-color);
  border-radius: 2px;
  width: 100%;
  height: 4px;
  transition: transform .15s ease-in-out;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scaleX(1);
}

.is-active:hover:after {
  transform: scaleX(1);
}

.is-active:link {
  color: #000;
}

.is-active:visited {
  color: gray;
}

.is-active:hover {
  -webkit-text-decoration-color: var(--link-color);
  text-decoration-color: var(--link-color);
}

.nav-item.is-active.apple-active a {
  color: var(--link-color);
}
